import Axios from "axios";
const HOME_ROUTE = "/home";

export default {
  async refreshMoviesAsync() {
    return await Axios.get(`${HOME_ROUTE}/refresh-movies/`);
  },
  async getSettingAsync() {
    return await Axios.get(`${HOME_ROUTE}/get-setting/`);
  },
  async getInitDataAsync() {
    return await Axios.get(`${HOME_ROUTE}/get-initial-data/`);
  },
  async getMoviesAsync() {
    return await Axios.get(`${HOME_ROUTE}/get-movies/`);
  },
  async getLandingPageMoviesAsync() {
    return await Axios.get(`${HOME_ROUTE}/get-landing-page-movies/`);
  },
  async getLandingPageMovieAsync() {
    return await Axios.get(`${HOME_ROUTE}/get-landing-page-movie/`);
  },
  async setFavoriteAsync(id) {
    return await Axios.get(`${HOME_ROUTE}/set-favorite/${id}`);
  },
  openInEdge(id) {
    return Axios.get(`${HOME_ROUTE}/open-in-edge/${id}`);
  }
};



