<template>
  <div>
    <div class="landing-page" :style="{ 'background-image': backgroundUrl }">
      <div class="landing-info">
        <b-card class="landing-info-header">
          <!-- Headline -->
          <div class="row">
            <div class="col-12 d-flex align-content-center flex-wrap pb-3">
              <div class="col-9">
                <div class="row">
                  <p
                    class="h1 title-overview text-light"
                    v-b-tooltip.hover
                    :title="landingMovie.displayTitle"
                  >
                    {{ landingMovie.displayTitle }}
                  </p>
                </div>
                <div class="row">
                  <span
                    class="text-muted h5 rating pl-1 m-0"
                    v-b-tooltip.hover
                    :title="$t('Release year')"
                    >{{ getReleaseYear }}</span
                  >
                  <a
                    :href="getTmdbLink"
                    target="_blank"
                    class="text-link h5 rating pl-1 m-0"
                    v-b-tooltip.hover
                    :title="$t('TMDB link')"
                    >TMDB</a
                  ><span class="text-muted h5 rating pl-1 m-0"> | </span>
                  <a
                    :href="getImdbLink"
                    target="_blank"
                    class="text-link h5 rating pl-1 m-0"
                    v-b-tooltip.hover
                    :title="$t('IMDB link')"
                    >IMDB</a
                  >
                  <a
                    href="#"
                    @click="favoriteClick(landingMovie.bId)"
                    v-b-tooltip
                    :title="
                      $t(
                        !landingMovie.isFavorite
                          ? 'Add to favorites'
                          : 'Remove from favorites'
                      )
                    "
                  >
                    <b-icon-star
                      v-if="!landingMovie.isFavorite"
                      class="text-warning rating-text pl-2"
                    ></b-icon-star>
                    <b-icon-star-fill
                      v-else
                      class="text-warning rating-text pl-2"
                    ></b-icon-star-fill>
                  </a>
                </div>
              </div>
              <div class="col-1 rating">
                <h6
                  class="text-danger float-right rating-text"
                  v-b-tooltip.hover
                  :title="$t('Rating')"
                >
                  {{ landingTmdbMovie.vote_average }}
                </h6>
              </div>
              <div class="col-2 rating">
                <h6
                  class="text-muted float-right popularity-text"
                  v-b-tooltip.hover
                  :title="$t('Popularity')"
                >
                  {{ landingTmdbMovie.popularity + "%" }}
                </h6>
              </div>
            </div>

            <div class="col-12 text-success">
              {{ getRuntime + " | " }}
              <template v-for="(genre, i) in landingTmdbMovie.genres">
                <span v-bind:key="genre.id">{{ genre.name }}</span
                >{{ i &lt; (landingTmdbMovie.genres.length - 1) ? ", " : "" }}
              </template>
              {{ " | " + getReleaseDate }}
              {{ " | " + landingMovie.sizeString }}
            </div>
          </div>

          <!-- Body -->
          <div class="row p-1">
            <div class="col-12">
              <p class="h5 text-muted overview">
                {{ landingTmdbMovie.overview }}
              </p>
            </div>
          </div>
        </b-card>

        <!-- Footer -->
        <div class="mt-5 ml-5 mr-5">
          <b-button
            variant="success"
            :to="/video-player/ + landingMovie.bId"
            :disabled="!landingMovie.isSupported"
          >
            <b-icon-play-btn-fill></b-icon-play-btn-fill>
            {{ $t("Play") }}
          </b-button>
          <b-button
            variant="outline-primary"
            class="landing-info-header mx-3"
            @click="moreInfoClick(landingMovie)"
          >
            <b-icon-info-circle-fill></b-icon-info-circle-fill>
            {{ $t("More info") }}
          </b-button>
          <div
            class="card landing-info-header mt-2"
            v-if="!landingMovie.isSupported"
          >
            <p class="text-center text-danger h5">
              {{
                $t(
                  "The '.{0}' format does not supported in web browsers! Sorry, you can not play the video!",
                  { 0: landingMovie.movieTypeText }
                )
              }}
            </p>
          </div>
          <div
            class="card landing-info-header mt-2"
            v-if="
              landingMovie.isSupported &&
              landingMovie.movieType == 2 &&
              getBrowserName != 'Microsoft Edge'
            "
          >
            <i18n
              path="The '.{0}' format does not supported in chrome browser! You can play in {1}!"
              tag="p"
              class="text-center text-danger h5"
            >
              <span place="0">{{ landingMovie.movieTypeText }}</span>
              <a
                place="1"
                href="javascript:void(0)"
                @click="openInEdge(landingMovie.bId)"
                >Microsoft Edge</a
              >
            </i18n>
          </div>
        </div>
      </div>
      <div class="overlay"></div>
    </div>

    <!-- Movies by categories -->
    <div v-if="movies.length && getCategories.length">
      <base-movie-card
        v-for="(category, index) in getCategories"
        :key="index"
        :title="$t(category.value)"
        :movies="createMoviesByCategory(category.key)"
      ></base-movie-card>
    </div>
  </div>
</template>

<script>
import HomeService from "@/api-services/home.service";
import Utils from "../../utils.js";
import { EventBus } from "../../event-bus.js";
import moment from "moment";
import Bowser from "bowser";
import BaseMovieCard from "@/components/Card/BaseMovieCard.vue";

export default {
  name: "Home",
  components: {
    BaseMovieCard,
  },
  data() {
    return {
      movies: [],
      landingMovie: {},
      landingTmdbMovie: {},
      isRefreshCompleted: false,
      backgroundUrl: null,
    };
  },
  methods: {
    async initialize() {
      EventBus.$emit("show-loading", this.$t("Loading movies...please wait.."));
      await this.getLandingPageMovie();
      await this.getLandingPageMovies(true);
    },
    async getLandingPageMovie() {
      await HomeService.getLandingPageMovieAsync()
        .then(async (response) => {
          if (response.status == 200) {
            this.landingMovie = Object.assign({}, response.data.localMovie);
            this.landingTmdbMovie = Object.assign({}, response.data.tmdbMovie);

            this.backgroundUrl = this.createBackgroundUrl(
              this.landingTmdbMovie.backdrop_path
            );
          }
        })
        .catch((err) => {
          Utils.showError(err);
        });
    },
    async getLandingPageMovies(isInit) {
      await HomeService.getMoviesAsync()
        .then(async (response) => {
          if (response.status == 200) {
            this.movies = response.data.movies;

            if (isInit) {
              this.$store.commit(
                "updatemovieCounts",
                Object.assign(
                  {},
                  {
                    localMoviesCount: response.data.localMoviesCount,
                    tmdbMoviesCount: response.data.tmdbMoviesCount,
                  }
                )
              );
            }
          }
        })
        .catch((err) => {
          Utils.showError(err);
        })
        .finally(() => {
          EventBus.$emit("hide-loading");
        });
    },
    async favoriteClick(id) {
      await HomeService.setFavoriteAsync(id)
        .then(async (response) => {
          if (response.status == 200) {
            let isFavorite = null;
            if (response.data.favoriteViewModel.isFavoriteNewState) {
              isFavorite = true;
            } else {
              isFavorite = false;
            }

            this.landingMovie.isFavorite = isFavorite;
            this.movies
              .filter((m) => m.key.bId == id)
              .map((m) => (m.key.isFavorite = isFavorite));
            Utils.alertModal(
              this.$t("Favorites"),
              this.$t(
                `Successfully ${
                  isFavorite ? "added to" : "removed from"
                } favorites`
              ),
              "success"
            );
          }
        })
        .catch((err) => {
          Utils.showError(err);
        });
    },
    openInEdge(id) {
      HomeService.openInEdge(id).catch((err) => {
        Utils.showError(err);
      });
    },
    createBackgroundUrl(url) {
      return (
        `url('` +
        this.$store.state.initData.setting.defaultCoverImagePath +
        url +
        `')`
      );
    },
    movieClick(id) {
      if (id) {
        let movie = this.movies.find((m) => m.key.bId == id).key;

        if (!movie.isSupported) {
          let message = this.$t(
            "The '.{0}' format does not supported in web browsers! Sorry, you can not play the video!",
            { 0: movie.movieTypeText }
          );
          Utils.alertModal(this.$t("Not supported format"), message, "danger");
        } else if (
          movie.isSupported &&
          movie.movieType == 2 &&
          this.getBrowserName != "Microsoft Edge"
        ) {
          let message = this.$t(
            "The '.{0}' format does not supported in chrome browser! Do you want to play in {1}?",
            { 0: movie.movieTypeText, 1: "Microsoft Edge" }
          );
          Utils.confirmModal(
            this.$t("Not supported format"),
            message,
            "primary",
            (confirm) => {
              if (confirm) {
                this.openInEdge(id);
              }
            }
          );
        } else {
          this.$router.push("/video-player/" + id);
        }
      }
    },
    moreInfoClick(movie) {
      Utils.movieModal(this.movies.find((m) => m.key.bId == movie.bId));
    },
    createMoviesByCategory(id) {
      if (this.movies.length) {
        switch (id) {
          // Favorites
          case 0:
            return this.movies.filter((m) => m.key.isFavorite);
          // Popular
          case 1:
            return this.movies.filter((m) => m.value.movie.popularity > 25);
          // Continue
          // case 2:
          //   return this.movies.filter((m) => m.key.isFavorite);
          // New, by creation date
          case 3:
            let byCreationDate = this.movies.slice();
            return byCreationDate.sort(
              (a, b) =>
                new Date(b.key.creationDate).valueOf() -
                new Date(a.key.creationDate).valueOf()
            );
          // New, by release date
          case 4:
            let byReleaseDate = this.movies.slice();
            return byReleaseDate.sort(
              (a, b) =>
                new Date(b.value.movie.release_date).valueOf() -
                new Date(a.value.movie.release_date).valueOf()
            );
          // Top rated
          case 5:
            return this.movies.filter((m) => m.value.movie.vote_average > 7);
        }
      }
    },
  },
  computed: {
    getRuntime() {
      if (this.landingTmdbMovie.runtime) {
        let hour = Math.floor(this.landingTmdbMovie.runtime / 60);
        let minute = this.landingTmdbMovie.runtime % 60;
        return (
          hour +
          this.$t("hour") +
          (minute ? " " + minute + this.$t("minute") : "")
        );
      }
    },
    getReleaseDate() {
      if (this.landingTmdbMovie.release_date) {
        return moment(this.landingTmdbMovie.release_date).format("YYYY-MM-DD");
      }
    },
    getReleaseYear() {
      if (this.landingTmdbMovie.release_date) {
        let releaseYear =
          "(" + moment(this.landingTmdbMovie.release_date).format("YYYY") + ")";
        return releaseYear;
      }
    },
    getTmdbLink() {
      if (this.landingTmdbMovie.id) {
        return (
          this.$store.state.initData.setting.defaultTmdbPath +
          this.landingTmdbMovie.id
        );
      }
    },
    getImdbLink() {
      if (this.landingTmdbMovie.imdb_id) {
        return (
          this.$store.state.initData.setting.defaultImdbPath +
          this.landingTmdbMovie.imdb_id
        );
      }
    },
    getBrowserName() {
      const browser = Bowser.getParser(window.navigator.userAgent);
      console.log(browser.getBrowserName());
      return browser.getBrowserName();
    },
    getCategories() {
      return this.$store.state.initData.movieCategories;
    },
  },
  async mounted() {
    await this.initialize();
  },
  async created() {
    EventBus.$on("favorite-click", (id) => {
      this.favoriteClick(id);
    });
    EventBus.$on("movie-click", (id) => {
      this.movieClick(id);
    });
  },
  beforeDestroy() {
    EventBus.$off("favorite-click");
    EventBus.$off("movie-click");
  },
};
</script>

<style lang="scss">
$blackblack: #000000;
$white: #ffffff;

body {
  background: $blackblack;
  color: $white;
}
.rating {
  align-self: center;
}
.rating-text {
  font-size: 2em;
  font-weight: bold;
}
.popularity-text {
  font-size: 1.5em;
  font-weight: bold;
}
.landing-info {
  position: relative;
  z-index: 4;
  width: 50%;
  left: 10vw;
  top: 10vw;
}
.movie-type-error {
  background-color: burlywood;
}
.landing-page {
  width: 100%;
  position: relative;
  background-size: cover;
  min-height: 800px;
}
.overlay {
  background: -moz-linear-gradient(top, #000000 0%, rgba(0, 0, 0, 0) 100%);
  background: -webkit-linear-gradient(top, #000000 0%, rgba(0, 0, 0, 0) 100%);
  background: linear-gradient(to bottom, #000000 0%, rgba(0, 0, 0, 0) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#a6000000", endColorstr="#00000000", GradientType=0 );
  height: 100%;
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  transform: rotate(180deg);
}
.favorite {
  position: absolute;
  right: 10px;
  top: 10px;
  pointer-events: all;
}
.overview {
  -webkit-line-clamp: 4;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}

@media screen and (max-width: 576px) {
  .landing-info {
    position: absolute;
    top: 10%;
    bottom: 10%;
    left: 1%;
    right: 1%;
    width: 94%;
    margin: auto;
  }
  .overview {
    -webkit-line-clamp: 2;
  }
}
</style>


