<template>
  <div class="px-3 py-2">
    <div class="wrapper">
      <p class="h2">{{ title }}</p>

      <carousel
        :per-page="6"
        :navigate-to="0"
        :mouse-drag="true"
        :paginationEnabled="false"
        :navigationEnabled="true"
        :navigationClickTargetSize="5"
      >
        <slide
          v-bind:key="movie.key.bId"
          v-for="movie in movies"
          class="mx-1 slider"
        >
          <div v-on:click="movieClick(movie)" class="image-wrapper">
            <img
              :src="getCoverPath + movie.value.movie.poster_path"
              class="image-poster"
            /></div></slide
      ></carousel>
    </div>
  </div>
</template>

<script>
import { Carousel, Slide } from "vue-carousel";
import Utils from "../../utils.js";

export default {
  name: "BaseMovieCard",
  components: {
    Carousel,
    Slide,
  },
  methods: {
    movieClick(movie) {
      Utils.movieModal(movie);
    },
  },
  computed: {
    getCoverPath() {
      return this.$store.state.initData.setting.defaultCoverImagePath;
    },
  },
  props: ["title", "movies"],
};
</script>

<style lang="scss">
.text-shadow {
  text-shadow: 0 0 4px #007bff;
}
.wrapper {
  width: 95%;
}
.slider {
  flex-basis: auto;
}
.image-wrapper {
  width: 250px;
  height: 300px;
  transition: transform 100ms ease-out;
}
.image-wrapper .image-poster {
  width: 100%;
  height: 100%;
  background-size: cover !important;
  background-position: center !important;
}
.image-wrapper:hover .image-poster {
  transform: scale(2);
  box-shadow: 0 0 2px #000a;
  cursor: pointer;
}
.VueCarousel-navigation-button[data-v-453ad8cd] {
  color: #e9e9e9 !important;
  outline: none !important;
}

@media only screen and (max-width: 599px) {
  .image-poster {
    height: 150px;
    width: 100px;
  }
}
</style>


